'use client'

import { useState, useCallback } from 'react'
import Cropper from 'react-easy-crop'
import { Button } from '@/components/ui/button'
import { Slider } from '@/components/ui/slider'
import { Check, X, ZoomIn } from 'lucide-react'

interface ImageCropperProps {
  image: string
  onCropComplete: (croppedImage: string) => void
  onCancel: () => void
}

interface CroppedArea {
  x: number
  y: number
  width: number
  height: number
}

export function ImageCropper({ image, onCropComplete, onCancel }: ImageCropperProps) {
  const [crop, setCrop] = useState({ x: 0, y: 0 })
  const [zoom, setZoom] = useState(1)
  const [croppedAreaPixels, setCroppedAreaPixels] = useState<CroppedArea | null>(null)

  const onCropChange = (location: { x: number; y: number }) => {
    setCrop(location)
  }

  const onZoomChange = (newZoom: number) => {
    setZoom(newZoom)
  }

  const onCropCompleteCallback = useCallback(
    (croppedArea: any, croppedAreaPixels: CroppedArea) => {
      setCroppedAreaPixels(croppedAreaPixels)
    },
    []
  )

  const createCroppedImage = async () => {
    if (!croppedAreaPixels) return

    try {
      const croppedImage = await getCroppedImg(image, croppedAreaPixels)
      onCropComplete(croppedImage)
    } catch (e) {
      console.error('è£åˆ‡å¤±æ•—:', e)
      alert('è£åˆ‡å¤±æ•—ï¼Œè«‹é‡è©¦')
    }
  }

  return (
    <div className="fixed inset-0 z-50 bg-black/90 flex flex-col">
      {/* è£åˆ‡å€åŸŸ */}
      <div className="flex-1 relative">
        <Cropper
          image={image}
          crop={crop}
          zoom={zoom}
          aspect={undefined}
          onCropChange={onCropChange}
          onZoomChange={onZoomChange}
          onCropComplete={onCropCompleteCallback}
        />
      </div>

      {/* æ§åˆ¶é¢æ¿ */}
      <div className="bg-gray-900 p-6 space-y-4">
        {/* ç¸®æ”¾æ§åˆ¶ */}
        <div className="flex items-center gap-4 max-w-md mx-auto">
          <ZoomIn className="w-5 h-5 text-white" />
          <Slider
            value={[zoom]}
            min={1}
            max={3}
            step={0.1}
            onValueChange={(values) => setZoom(values[0])}
            className="flex-1"
          />
          <span className="text-white text-sm w-12 text-right">{Math.round(zoom * 100)}%</span>
        </div>

        {/* æç¤ºæ–‡å­— */}
        <p className="text-center text-white text-sm">
          æ‹–å‹•åœ–ç‰‡èª¿æ•´ä½ç½®ï¼Œä½¿ç”¨æ»‘æ¡¿æˆ–æ»¾è¼ªç¸®æ”¾ï¼Œæ‹–å‹•é‚Šè§’èª¿æ•´è£åˆ‡ç¯„åœ
        </p>

        {/* æŒ‰éˆ• */}
        <div className="flex gap-3 justify-center">
          <Button
            type="button"
            variant="outline"
            size="lg"
            onClick={onCancel}
            className="min-w-32"
          >
            <X className="w-4 h-4 mr-2" />
            å–æ¶ˆ
          </Button>
          <Button
            type="button"
            size="lg"
            onClick={createCroppedImage}
            className="min-w-32 bg-blue-600 hover:bg-blue-700"
          >
            <Check className="w-4 h-4 mr-2" />
            ç¢ºèªè£åˆ‡
          </Button>
        </div>
      </div>
    </div>
  )
}

// è£åˆ‡åœ–ç‰‡çš„è¼”åŠ©å‡½æ•¸
async function getCroppedImg(imageSrc: string, pixelCrop: CroppedArea): Promise<string> {
  const image = await createImage(imageSrc)
  const canvas = document.createElement('canvas')
  const ctx = canvas.getContext('2d')

  if (!ctx) {
    throw new Error('ç„¡æ³•å–å¾— canvas context')
  }

  // è¨­å®š canvas å¤§å°ç‚ºè£åˆ‡å€åŸŸå¤§å°
  canvas.width = pixelCrop.width
  canvas.height = pixelCrop.height

  // ç¹ªè£½è£åˆ‡å¾Œçš„åœ–ç‰‡
  ctx.drawImage(
    image,
    pixelCrop.x,
    pixelCrop.y,
    pixelCrop.width,
    pixelCrop.height,
    0,
    0,
    pixelCrop.width,
    pixelCrop.height
  )

  // è½‰æ›ç‚º base64
  return canvas.toDataURL('image/png')
}

// å»ºç«‹ Image ç‰©ä»¶
function createImage(url: string): Promise<HTMLImageElement> {
  return new Promise((resolve, reject) => {
    const image = new Image()
    image.addEventListener('load', () => resolve(image))
    image.addEventListener('error', (error) => reject(error))
    image.src = url
  })
}
