import { create } from 'zustand'
import { persist } from 'zustand/middleware'
import type {
  CaseData,
  PatientInfo,
  MedicalHistoryType,
  SymptomType,
  ClinicalCourse,
  Examination,
  RiskAssessment,
} from '@/types'

interface CaseStore {
  // ç•¶å‰ç·¨è¼¯çš„æ¡ˆä¾‹
  currentCase: CaseData | null

  // åˆå§‹åŒ–æ–°æ¡ˆä¾‹
  initializeCase: () => void

  // æ›´æ–°ç—…æ‚£åŸºæœ¬è³‡æ–™
  updatePatientInfo: (patientInfo: PatientInfo) => void

  // æ›´æ–°ç—…å²
  updateMedicalHistory: (history: MedicalHistoryType[]) => void

  // æ›´æ–°è‡ªè¨‚ç—…å²
  updateCustomHistory: (customHistory: string) => void

  // æ›´æ–°ç—‡ç‹€
  updateSymptoms: (symptoms: SymptomType[]) => void

  // æ›´æ–°è‡ªè¨‚ç—‡ç‹€
  updateCustomSymptoms: (customSymptoms: string) => void

  // æ›´æ–°ç—‡ç‹€ç™¼ç”Ÿæ™‚é–“
  updateSymptomOnset: (onset: string) => void

  // æ›´æ–°å°±é†«æ­·ç¨‹
  updateClinicalCourse: (course: ClinicalCourse) => void

  // æ–°å¢æª¢æŸ¥å ±å‘Š
  addExamination: (exam: Examination) => void

  // æ›´æ–°æª¢æŸ¥å ±å‘Š
  updateExamination: (id: string, exam: Partial<Examination>) => void

  // åˆªé™¤æª¢æŸ¥å ±å‘Š
  removeExamination: (id: string) => void

  // æ›´æ–°æ‰‹è¡“é¢¨éšªè©•ä¼°
  updateRiskAssessment: (assessment: RiskAssessment) => void

  // æ›´æ–°åŠŸèƒ½ç‹€æ…‹
  updateFunctionalStatus: (status: string) => void

  // æ›´æ–°é å¾Œè©•ä¼°
  updatePrognosis: (prognosis: string) => void

  // æ›´æ–°ç”Ÿæˆçš„æ–‡ä»¶
  updateGeneratedDocument: (document: string) => void

  // æ¸…ç©ºç•¶å‰æ¡ˆä¾‹
  clearCase: () => void
}

export const useCaseStore = create<CaseStore>()(
  persist(
    (set) => ({
      currentCase: null,

      initializeCase: () => {
        const now = new Date().toISOString()
        set({
          currentCase: {
            id: `case-${Date.now()}`,
            createdAt: now,
            updatedAt: now,
            patient: {
              name: '',
              chartNumber: '',
              gender: 'female',
              age: 0,
              birthDate: '',
              nationalId: '',
            },
            medicalHistory: [],
            customHistory: '',
            symptoms: [],
            customSymptoms: '',
            symptomOnset: '',
            clinicalCourse: {
              previousCare: '',
              presentation: '',
            },
            examinations: [],
            riskAssessment: {
              surgeon1: '',
              surgeon2: '',
            },
            functionalStatus: '',
            prognosis: '',
          },
        })
      },

      updatePatientInfo: (patientInfo) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                patient: patientInfo,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateMedicalHistory: (history) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                medicalHistory: history,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateCustomHistory: (customHistory) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                customHistory,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateSymptoms: (symptoms) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                symptoms,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateCustomSymptoms: (customSymptoms) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                customSymptoms,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateSymptomOnset: (onset) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                symptomOnset: onset,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateClinicalCourse: (course) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                clinicalCourse: course,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      addExamination: (exam) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                examinations: [...state.currentCase.examinations, exam],
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateExamination: (id, exam) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                examinations: state.currentCase.examinations.map((e) =>
                  e.id === id ? { ...e, ...exam } : e
                ),
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      removeExamination: (id) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                examinations: state.currentCase.examinations.filter(
                  (e) => e.id !== id
                ),
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateRiskAssessment: (assessment) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                riskAssessment: assessment,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateFunctionalStatus: (status) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                functionalStatus: status,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updatePrognosis: (prognosis) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                prognosis,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      updateGeneratedDocument: (document) =>
        set((state) => ({
          currentCase: state.currentCase
            ? {
                ...state.currentCase,
                generatedDocument: document,
                updatedAt: new Date().toISOString(),
              }
            : null,
        })),

      clearCase: () => set({ currentCase: null }),
    }),
    {
      name: 'tavi-case-storage', // LocalStorage key
      partialize: (state) => ({ currentCase: state.currentCase }), // åªæŒä¹…åŒ– currentCase
    }
  )
)
